@import './mixins';

.custome-button {
    position: relative;
    padding: 12px 16px;
    background-color: transparent;
    // width: fit-content;
    font-family: Inter Tight;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.96px;
    text-transform: uppercase;
    border: 1px solid #AAA;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0 6px;
    text-decoration: none;
    overflow: hidden;
    max-width: 100%;
    white-space: nowrap;
    transition: all .3s ease;




    &-light {

        &,
        &:visited,
        &:active,
        &:focus {
            color: #3F3F3E;

        }
    }

    &-dark {

        &,
        &:visited,
        &:active,
        &:focus {
            color: #EEEEEE;
        }
    }

    svg {
        width: 0;
        visibility: hidden;
        opacity: 0;
        transform: translateX(100px);
        transition: all .3s ease;
    }

    &:hover {

        svg {
            width: auto;
            visibility: visible;
            opacity: 1;
            transform: translateX(0);

            transition: all .3s ease;
        }
    }

    @include respond(sm-tab-port) {
        font-size: 14px;
        border: 0;

        svg {
            width: auto;
            visibility: visible;
            opacity: 1;
        }
    }

}